























































































import { Vue, Component } from 'vue-property-decorator'
import { addMajor, changeMajor, deleteMajor, getMajors } from 'utils/api'

@Component({})
export default class SetProfessions extends Vue {
  professions: any = []
  selected: any = []
  listLoading: boolean = false

  editFormVisible: boolean = false // 是否显示编辑界面
  editLoading: boolean = false
  editForm: any = {
    name: '',
    time: '',
    acceptArt: '',
    comment: '',
    college: '',
    stuNum: ''
  }
  editFormWidth: string = '120px'

  addFormVisible: boolean = false // 是否显示新增界面
  addLoading: boolean = false
  addForm: any = {
    name: '',
    time: '',
    acceptArt: '',
    comment: '',
    college: '',
    stuNum: ''
  }
  addFormRules: any = {
    name: [
      { required: true, message: '请输入招生专业（类）名称', trigger: 'change' }
    ],
    time: [
      { required: true, message: '请输入学制', trigger: 'change' }
    ],
    comment: [
      { required: true, message: '请输入所含一流学科名称', trigger: 'change' }
    ],
    college: [
      { required: true, message: '请输入所属院系', trigger: 'change' }
    ],
    stuNum: [
      { required: true, message: '请输入招生人数', trigger: 'change' }
    ]
  }
  addFormWidth: string = '180px'

  mounted () {
    this.$nextTick(() => {
      this.getProfessions()
    })
  }

  getProfessions () {
    // 获取招生专业
    getMajors()
      .then(res => {
        this.professions = res.data
        this.professions.forEach(major => {
          major.acceptArt = major.acceptArt ? '文理兼收' : '仅限理科'
        })
      })
      .catch(err => {
        this.$message({
          message: err.toString(),
          type: 'error'
        })
      })
  }

  handleEdit (row: any) {
    // 编辑
    this.editFormVisible = true
    this.editForm = (<any> Object).assign({}, row)
  }

  editSubmit () {
    let form = this.editForm
    form.majorid = form.id
    form.acceptArt = form.acceptArt === '文理兼收'
    changeMajor(form)
      .then(res => {
        if (res.data.succeed) {
          this.$message({
            message: '编辑成功',
            type: 'success'
          })
          this.editFormVisible = false
          this.getProfessions()
        } else {
          this.$message({
            message: res.data.msg,
            type: 'error'
          })
        }
      })
      .catch(err => {
        this.$message({
          message: err.toString(),
          type: 'error'
        })
      })
  }

  addSubmit () {
    // 新增
    let form = this.addForm
    form.acceptArt = form.acceptArt === '文理兼收'
    addMajor(form)
      .then(res => {
        if (res.data.succeed) {
          this.$message({
            message: '添加成功',
            type: 'success'
          })
          this.addFormVisible = false
          this.getProfessions()
        } else {
          this.$message({
            message: res.data.msg,
            type: 'error'
          })
        }
      })
      .catch(err => {
        this.$message({
          message: err.toString(),
          type: 'error'
        })
      })
  }

  handleRemove (row: any) {
    // 删除
    this.$confirm('确认删除？')
      .then(() => {
        deleteMajor({
          majorid: row.id
        }).then(res => {
          if (res.data.succeed) {
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.getProfessions()
          } else {
            this.$message({
              message: res.data.msg,
              type: 'error'
            })
          }
        }).catch(err => {
          this.$message({
            message: err.toString(),
            type: 'error'
          })
        })
      })
      .catch(() => {
      })
  }

  batchRemove () {
    // 批量删除
    this.$confirm('确认删除？')
      .then(() => {
        this.selected.forEach(major => {
          deleteMajor({
            majorid: major.id
          }).then(res => {
            if (res.data.succeed) {
              this.$message({
                message: '删除成功',
                type: 'success'
              })
              this.getProfessions()
            } else {
              this.$message({
                message: res.data.msg,
                type: 'error'
              })
            }
          }).catch(err => {
            this.$message({
              message: err.toString(),
              type: 'error'
            })
          })
        })
      }).catch(() => {
    })
  }

  handleCurrentChange (val) {
    this.selected = val
  }

  handleDialogClose () {
    let _this: any = this
    _this.$confirm('确认关闭？')
      .then(() => {
        this.editFormVisible = false
        this.addFormVisible = false
      })
      .catch(() => {
      })
  }
}



