

















































import { Vue, Component } from 'vue-property-decorator'
import { getDDL, registerUser } from 'utils/api'
import highSchools from 'utils/highSchools.ts'

@Component({})
export default class Register extends Vue {
  hasClosed: boolean = false
  schoolOptions: any = highSchools
  registerForm: any = {
    name: '',
    idCardNumber: '',
    password: '',
    confirmPassword: '',
    email: '',
    tel: '',
    birthDate: '',
    address: '',
    highSchool: []
  }
  registerRules: any = {
    name: [
      { required: true, message: '請輸入姓名', trigger: 'change' }
    ],
    idCardNumber: [
      {
        required: true,
        validator: (rule, value, callback) => {
          if (value === '') {
            callback(new Error('請輸入身份證號'))
          } else if (!/^[A-Z][0-9]{9}$/.test(value)) {
            callback(new Error('請檢查身份證是否正確，首字母須大寫'))
          } else {
            callback()
          }
        }, trigger: 'blur'
      }
    ],
    password: [
      { required: true, message: '請輸入密碼', trigger: 'change' },
      { min: 9, message: '長度至少為9位', trigger: 'blur' }
    ],
    confirmPassword: [
      {
        required: true,
        validator: (rule, value, callback) => {
          if (value === '') {
            callback(new Error('請再次輸入密碼'))
          } else if (value !== this.registerForm.password) {
            callback(new Error('兩次輸入密碼不一致'))
          } else {
            callback()
          }
        }, trigger: 'blur'
      }
    ],
    email: [
      {
        required: true,
        validator: (rule, value, callback) => {
          if (value === '') {
            callback(new Error('請輸入郵箱'))
          } else if (!/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(value)) {
            callback(new Error('郵箱格式錯誤'))
          } else {
            callback()
          }
        },
        trigger: 'blur'
      }
    ]
  }
  registerFormWidth: string = '120px'

  mounted () {
    getDDL()
      .then(res => {
        this.hasClosed = new Date() >= new Date(res.data.ddl)
      })
      .catch(err => {
        this.hasClosed = false
        this.$message({
          message: err.toString(),
          type: 'error'
        })
      })
  }

  registerStudent () {
    let form: any = this.$refs.registerForm
    form.validate((valid) => {
      if (valid) {
        let form: any = (<any> Object).assign({}, this.registerForm)
        form.highSchool = this.registerForm.highSchool.toString()
        registerUser(form)
          .then((res) => {
            if (res.data.succeed) {
              this.$message.success('註冊成功')
              this.$router.push('/')
            } else {
              this.$message.error(res.data.msg)
            }
          })
          .catch((err) => {
            this.$message.error(err.toString())
          })
      } else {
        this.$message.error('內容不符合要求，請重新填寫')
      }
    })
  }
}



