












































































import { Vue, Component } from 'vue-property-decorator'
import { delToken, getEmailAddress, getStudentToken } from 'utils/token.ts'
import Message from './Message.vue'
import { getResultMessage, studentGetMessage } from 'utils/api'

@Component({
  components: { Message }
})
export default class Student extends Vue {
  systemName: string = '台灣免試生系統'
  collapsed: boolean = false//是否折疊（默認否）
  userName: string = ''
  userAvatar: string = ''
  //收到的消息
  hasNewMessage: boolean = false
  hasAccess: boolean = false
  token: string = ''

  get navItems () {
    return (<any> this.$router).options.routes.filter(route => {
      return route.path === '/student'
    })
  }

  mounted () {
    this.token = getStudentToken()
    const email = getEmailAddress()
    /*如果token不存在，則跳轉到登錄頁*/
    if (this.token == '') {
      this.$router.push('/')
    }
    if (email) {
      getResultMessage({
        email: email
      }).then(res => {
        if (res.data.succ && res.data.from !== 1) {
          this.hasAccess = true
        }
      }).catch(err => {
        //
      })
    }
  }

  // handleNewMessage () {
  //   studentGetMessage()
  //     .then(res => {
  //       this.hasNewMessage = res.data.length > 0
  //     })
  //     .catch(err => {
  //       this.hasNewMessage = false
  //     })
  // }

  handleCollapse () {
    this.collapsed = !this.collapsed
  }

  backToIndex () {
    this.$router.push('/student')
  }

  logout () {
    this.$confirm('確認退出?', '提示', {})
      .then(() => {
        delToken()
        this.$router.push('/')
      })
      .catch((err) => {
        // 啥都不做
      })
  }
}



