










































import { Vue, Component } from 'vue-property-decorator'
import { adminDeleteMessage, adminGetMessage, adminUpdateMessage } from 'utils/api'

@Component({})
export default class ViewMessage extends Vue {
  messages: any = []
  selections: any = []
  editForm: any = {
    id: '',
    title: '',
    content: '',
    releasedTime: ''
  }
  editFormRules: any = {
    title: [
      { required: true, message: '请填写标题', trigger: 'blur' }
    ],
    content: [
      { required: true, message: '请填写内容', trigger: 'blur' }
    ]
  }
  editFormVisible: boolean = false
  messagesLoading: boolean = false

  mounted () {
    this.getMessages()
  }

  handleSelectionChange (val) {
    this.selections = val
  }

  getMessages () {
    this.messagesLoading = true
    adminGetMessage()
      .then((res) => {
        if (res.data) {
          let msg = []
          res.data.forEach((datum) => {
            datum.releasedTime = datum.releasedTime.split('T')[0]
            msg.push(datum)
          })
          this.messages = msg
        } else {
          this.$message({
            message: '读取失败',
            type: 'error'
          })
        }
        this.messagesLoading = false
      })
      .catch((err) => {
        this.$message({
          message: err.toString(),
          type: 'error'
        })
        this.messagesLoading = false
      })
  }

  editMessage (index, row) {
    this.editFormVisible = true
    this.editForm = {
      id: index,
      title: row.title,
      content: row.content,
      releasedTime: row.releasedTime
    }
  }

  batchDeleteMessage () {
    let ids = []
    this.selections.forEach(select => {
      ids.push(select.id)
    })
    this.deleteMessage(ids)
  }

  deleteSingleMessage (row) {
    let ids = []
    ids.push(row.id)
    this.deleteMessage(ids)
  }

  deleteMessage (ids) {
    let _this = this
    this.$confirm('确认删除？')
      .then(() => {
        adminDeleteMessage({ data: { ids: ids } })
          .then((res) => {
            if (res.data) {
              this.$message({
                message: '删除成功',
                type: 'success'
              })
            } else {
              this.$message({
                message: '删除失败',
                type: 'error'
              })
            }
            return _this
          }).then(that => {
          that.getMessages()
        }).catch((err) => {
          this.$message({
            message: err.toString(),
            type: 'error'
          })
        })
      })
      .catch(() => {
      })
  }

  editSubmit () {
    this.messages[this.editForm.id].title = this.editForm.title
    this.messages[this.editForm.id].content = this.editForm.content
    adminUpdateMessage({
      messages: this.messages
    }).then((res) => {
      if (res.data.succeed) {
        this.$message({
          message: '编辑成功',
          type: 'success'
        })
        this.editFormVisible = false
      } else {
        this.$message({
          message: res.data.msg,
          type: 'error'
        })
      }
    }).catch((err) => {
      this.$message({
        message: err.toString(),
        type: 'error'
      })
    })
  }

  handleEditClose () {
    this.$confirm('确认关闭？')
      .then(() => {
        this.editFormVisible = false
      })
      .catch(() => {
      })
  }
}



