















































































import { Vue, Component } from 'vue-property-decorator'
import { delToken, getAdminToken } from 'utils/token.ts'
import Settings from '@/views/admin/Settings.vue'

@Component({
  components: {
    Settings
  }
})
export default class Admin extends Vue {
  systemName: string = '台湾免试生管理系统'
  collapsed: boolean = false//是否折叠（默认否）
  token: string = ''

  get navItems () {
    return (<any> this.$router).options.routes.filter(route => {
      return route.path === '/admin' && !route.hidden
    })
  }

  mounted () {
    this.token = getAdminToken()
    /*如果cookie不存在，则跳转到登录页*/
    if (this.token == '') {
      this.$router.push('/')
    }
  }

  handleCollapse () {
    this.collapsed = !this.collapsed
  }

  backToIndex () {
    this.$router.push('/admin')
  }

  logout () {
    this.$confirm('确认退出?', '提示', {})
      .then(() => {
        delToken()
        this.$router.push('/')
      })
      .catch(() => {
      })
  }
}



